* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: inherit;
}
body {
  line-height: 1;
  color: #333;
}
.header {
  width: 100%;
  height: 60px;
  background-color: #ffffff;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.header figure {
  height: 30px;
  margin: 0 15px;

}
.header figure img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.header h1 {
  font-size: 20px;
}
.main_visual {
  width: 100%;
}
.main_visual .bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.main_visual .icons {
  margin: 100px auto;
  display: flex;
  overflow-Y: scroll;
}
.main_visual .icons p {
  height: 60px;
  font-size: 30px;
  margin-left: 50px;
}

.tour_list {
  max-width: 767px;
  width: calc(100% - 30px);
  margin: 40px auto;
  img {
    width: 100%;
  }
}
.item {
  position: relative;
}
.item figure {
  border-radius: 30px;
  overflow: hidden;
}
.item h4 {
  font-size: 25px;
  padding: 20px 0;
}

.item--text {
  padding-bottom:50px;
  p{
    font-size: 25px;
    padding-right: 20px;
    padding-bottom: 10px;
    
  }
  span {font-size: 20px;
  line-height: 1.3;}
}